import axios from 'axios'
import { Toast } from 'vant'
class SetMusicData {
    //获取歌词
    getSongWordData(item, index, id) {
        Toast.loading({
            message: "加载中...",
            forbidClick: true,
            duration: 0,
            loadingType: "spinner",
        });
        axios({
                method: "get",
                url: "/lyric",
                params: {
                    id,
                },
            })
            .then((result) => {
                //无歌词情况：1.纯音乐 2.无歌词源
                if (Object.keys(result.data).indexOf("lrc") < 0) {
                    if (Object.keys(result.data).indexOf("nolyric") >= 0) {
                        //纯音乐 《纯纯的音乐》
                        let data = [{
                            nolyric: true,
                            title: "纯音乐，请欣赏",
                            isActive: false,
                        }, ];
                        item[index].songWordData = data;
                    } else {
                        //无歌词源 《sca》
                        let data = [{
                            nolyric: true,
                            title: "暂无歌词",
                            isActive: false,
                        }, ];
                        item[index].songWordData = data;
                    }
                }
                //有歌词情况：1.没有字 2.有字
                else {
                    if (result.data.lrc.lyric == "") {
                        let data = [{
                            nolyric: true,
                            title: "纯音乐，请欣赏",
                            isActive: false,
                        }, ];
                        item[index].songWordData = data;
                    } else {
                        let reg = "\\[.*\\]";
                        let sonArray = result.data.lrc.lyric.split("\n");

                        let data = [];
                        for (let i = 0; i < sonArray.length; i++) {
                            //没有包括[]
                            // 判断是否含有[00:00]格式
                            let timekey2 = "";
                            let timekey = "";
                            if (sonArray[i].indexOf(0) > -1) {
                                //包括[]
                                timekey = sonArray[i].match(reg)[0];
                                timekey2 = sonArray[i].match(/[0-9]\d:[0-9]\d/)[0];
                            } else {
                                timekey2 = "";
                            }
                            //拿到歌词文字
                            let timeLength = timekey.split("").length;
                            let songText = sonArray[i].slice(timeLength);
                            //歌词文字为空跳过该循环
                            if (songText == "") {
                                continue;
                            }
                            data.push({
                                time: timekey2,
                                title: songText,
                                isActive: false,
                            });
                            item[index].songWordData = data;
                        }
                    }
                }

                Toast.clear();
            })
            .catch((err) => {
                Toast.clear();
            });
    }

    //获取歌手专辑
    getSingerAlbum(item, index, id) {
        Toast.loading({
            message: "加载中...",
            forbidClick: true,
            duration: 0,
            loadingType: "spinner",
        });
        axios({
                method: "get",
                url: "/artist/album",
                params: {
                    id,
                },
            })
            .then((result) => {
                Toast.clear();
                item[index].singerImg = result.data.artist.picUrl;
            })
            .catch((err) => {
                Toast.clear();
            });
    }
}
export const setMusicData = new SetMusicData();