class Utils {
    //序列化参数
    queryString(o) {

        let params = "";
        for (let key in o) {
            params += `${key}=${o[key]}&`;
        }
        return params.slice(0, -1)
    }

    //将时间转换成秒数
    // 02:00
    setGoS(time) {
        let m = Number(time.slice(0, 2))
        let s = Number(time.slice(3));
        return m * 60 + s
    }
}
export const utils = new Utils();