<template>
    <div class="hotword">
        <div
            class="bgImgBox"
            :style="{ backgroundImage: 'url(' + showSongUrl + ')' }"
        ></div>

        <div class="hotwordTop flexbetween">
            <div class="hotwordTopLeft" @click="getBack">
                <div class="icon">
                    <van-icon name="arrow-left" size="20" />
                </div>
                <div class="text">热评墙</div>
            </div>
            <div class="hotwordTopRight">
                <span>{{ nowShowNum }}/{{ allNum }}</span>
            </div>
        </div>
        <div class="hotwordBody">
            <div class="hotWall" :style="{ width: hotWallWidth + 'px' }">
                <div
                    class="hotWallItem"
                    @touchstart="touchShotwordBody($event, index)"
                    @touchmove="touchMhotwordBody($event, index)"
                    @touchend="touchEhotwordBody($event, index)"
                    :style="{ width: itemWidth + 'px' }"
                    v-for="(item, index) in hotWallData"
                    :key="index"
                >
                    <div class="arBox">
                        <img :src="item.userUrl" />
                        <span>{{ item.nickName }}</span>
                    </div>
                    <div class="douhao">
                        <img src="../assets/img/shuangyh.svg" alt />
                    </div>
                    <div class="cententBox">
                        <span>{{ item.content }}</span>
                    </div>
                    <div class="songBox">
                        <div class="songName" @click="clicksetUrl(index)">
                            <span>{{ item.songName }}</span>
                        </div>
                        <div class="liveBox">
                            <img src="../assets/img/ai.svg" alt />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import "../assets/less/hotword.less";
import { utils } from "../assets/js/utils";
import { createNamespacedHelpers } from "vuex";
import { setMusicData } from "../assets/js/setMusicData";
const {
    mapState: musicState,
    mapActions: musicActions
} = createNamespacedHelpers("MusicAudioStore");
export default {
    name: "Hotword",
    data() {
        return {
            //背景图片
            showSongUrl: "",
            // 热评榜数据
            hotWallData: [],
            // 歌曲数据
            songData: [],
            //总数据
            allHotWallData: [],
            //item宽度
            itemWidth: "",
            //大盒子宽度
            hotWallWidth: "",
            //按下位置
            startPagex: "",
            //松开位置
            endPagex: "",
            //按下标识
            touchBool: false,
            //设置hotWallLeftNum
            hotWallLeftNum: "",
            //当前页数
            nowShowNum: 1,
            //总数量
            allNum: ""
        };
    },
    computed: {
        ...musicState(["hotWallPageNum"])
    },
    created() {
        //设置音乐组件隐藏
        this.setaudioxy(2);
        //获取云村热评
        this.getHotWall();
        //设置宽度
        this.setItemWidth();
    },
    methods: {
        ...musicActions([
            "setMusicUrl",
            "setaudioData",
            "setaudioxy",
            "seticonNum",
            "setsongData",
            "setHotWallPageNum",
            "setSongListData",
            "setClickSong"
        ]),

        //获取云村热评
        getHotWall() {
            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner"
            });
            this.axios({
                method: "GET",
                url: "/comment/hotwall/list",
                params: {}
            })
                .then(result => {
                    this.$toast.clear();

                    if ((result.status = "200")) {
                        this.hotWallData = [];
                        let data = [];
                        //设置总数量
                        this.allNum = result.data.data.length;

                        for (let i = 0; i < result.data.data.length; i++) {
                            data.push({
                                nickName:
                                    result.data.data[i].simpleUserInfo.nickname,
                                userUrl:
                                    result.data.data[i].simpleUserInfo.avatar,
                                content: result.data.data[i].content,
                                songName:
                                    result.data.data[i].simpleResourceInfo.song
                                        .name,
                                songUrl:
                                    result.data.data[i].simpleResourceInfo
                                        .songCoverUrl,
                                songId:
                                    result.data.data[i].simpleResourceInfo
                                        .songId
                            });
                        }
                        let ids = [];
                        data.map(v => {
                            this.hotWallData.push(v);
                            ids.push(v.songId);
                        });
                        ids = ids.join(",");
                        // 获取歌曲详情
                        this.getsongDatabyId(ids);
                        //设置背景图
                        this.setShowSongUrl(
                            this.hotWallData[this.hotWallPageNum]
                        );
                        //设置字体显示
                        let hotWall = document.querySelector(".hotWall");
                        hotWall.style.left =
                            -this.itemWidth * this.hotWallPageNum + "px";
                        //设置页码
                        this.nowShowNum = this.hotWallPageNum + 1;
                    }
                })
                .catch(err => {
                    this.$toast.clear();
                });
        },

        //设置背景图
        setShowSongUrl(data) {
            this.showSongUrl = data.songUrl;
        },

        //返回
        getBack() {
            this.$router.go(-1);
            //设置热评墙页码储存在vuex
            this.setHotWallPageNum(0);
        },

        //获取歌单歌曲详情
        getsongDatabyId(ids) {
            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner"
            });
            this.axios({
                method: "get",
                url: "/song/detail",
                params: {
                    ids
                }
            })
                .then(result => {
                    this.$toast.clear();
                    this.songData = [];
                    result.data.songs.map(v => {
                        this.songData.push(v);
                    });

                    // 获取歌单的播放url
                    this.songData.map((v, index) => {
                        //设置歌手名称
                        let songar = "";
                        v.ar.map(arv => {
                            songar += arv.name + "/";
                        });
                        v.songAr = songar.slice(0, -1);
                        //设置播放音乐链接
                        v.palyurl =
                            "https://music.163.com/song/media/outer/url?id=" +
                            v.id +
                            ".mp3";
                        //设置active
                        v.isActive = false;
                        // //获取歌手专辑（获取歌手图片）
                        // this.getSingerAlbum(index, v.ar[0].id);
                        //获取歌手专辑（获取歌手图片）
                        setMusicData.getSingerAlbum(
                            this.songData,
                            index,
                            v.ar[0].id
                        );

                        //设置总时间
                        let m = Math.floor((v.dt / 1000 / 60) % 60);
                        let s = Math.floor((v.dt / 1000) % 60);
                        v.totalTime = m + "." + s;

                        //设置歌词
                        //获取歌词
                        setMusicData.getSongWordData(
                            this.songData,
                            index,
                            v.id
                        );
                    });
                })
                .catch(err => {
                    this.$toast.clear();
                });
        },

        //获取歌词 无用
        getSingerData(index, id) {
            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner"
            });
            this.axios({
                method: "get",
                url: "/lyric",
                params: {
                    id
                }
            })
                .then(result => {
                    //无歌词，歌词无内容
                    if (result.data.nolyric || result.data.lrc.lyric == "") {
                        let data = [
                            {
                                nolyric: true,
                                title: "纯音乐，请欣赏",
                                isActive: false
                            }
                        ];
                        this.songData[index].songWordData = data;
                    } else {
                        let reg = "\\[.*\\]";

                        let sonArray = result.data.lrc.lyric.split("\n");
                        let data = [];
                        for (let i = 0; i < sonArray.length; i++) {
                            //包括[]
                            let timekey = sonArray[i].match(reg)[0];

                            //没有包括[]
                            // 判断是否含有[00:00]格式
                            let timekey2 = "";
                            if (sonArray[i].indexOf(0) > -1) {
                                timekey2 = sonArray[i].match(
                                    /[0-9]\d:[0-9]\d/
                                )[0];
                            } else {
                                timekey2 = "";
                            }

                            let timeLength = timekey.split("").length;
                            let songText = sonArray[i].slice(timeLength);
                            if (songText == "") {
                                continue;
                            }
                            data.push({
                                time: timekey2,
                                title: songText,
                                isActive: false
                            });

                            this.songData[index].songWordData = data;
                        }
                    }
                    this.$toast.clear();
                })
                .catch(err => {
                    this.$toast.clear();
                });
        },

        //获取歌手专辑 无用
        getSingerAlbum(index, id) {
            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner"
            });
            this.axios({
                method: "get",
                url: "/artist/album",
                params: {
                    id
                }
            })
                .then(result => {
                    this.$toast.clear();
                    this.songData[index].singerImg = result.data.artist.picUrl;
                })
                .catch(err => {
                    this.$toast.clear();
                });
        },

        //点击设置音乐播放并跳转
        clicksetUrl(index) {
            //设置音乐组件
            this.setClickSong(true);
            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner"
            });
            //传值到state
            this.setsongData(this.songData[index]);
            this.seticonNum(1);
            //跳转
            this.$router.push({ name: "Songdetail" });
        },

        //设置宽度
        setItemWidth() {
            this.itemWidth = document.body.clientWidth;
        },

        //按下
        touchShotwordBody(e, index) {
            this.startPagex = "";
            this.startPagex = e.changedTouches[0].pageX;
            this.touchBool = true;
            let hotWall = document.querySelector(".hotWall");
            hotWall.style.transitionDuration = "0s";
            this.hotWallLeftNum = hotWall.offsetLeft;
        },

        //移动
        touchMhotwordBody(e, index) {
            let movePagex = e.changedTouches[0].pageX;
            let hotWall = document.querySelector(".hotWall");

            let hotWallLeft = "";
            if (movePagex - this.startPagex >= 0) {
                // 第一个不允许向右划
                if (this.hotWallLeftNum == "0") {
                    return;
                }
                hotWallLeft = this.hotWallLeftNum + movePagex - this.startPagex;
            } else {
                //设置无法左划
                if (index == this.hotWallData.length - 1) {
                    return;
                }
                hotWallLeft = this.hotWallLeftNum + movePagex - this.startPagex;
            }
            hotWall.style.left = hotWallLeft + "px";
        },

        //离开
        touchEhotwordBody(e, index) {
            this.endPagex = "";
            this.endPagex = e.changedTouches[0].pageX;
            this.touchBool = false;

            let chaNum = this.endPagex - this.startPagex;
            let hotWall = document.querySelector(".hotWall");

            hotWall.style.transitionDuration = "0.3s";
            // 未达移动条件
            if (Math.abs(chaNum) < this.itemWidth / 2) {
                hotWall.style.left = this.hotWallLeftNum + "px";
            }
            //达到移动条件
            else {
                // 左划
                if (chaNum <= -this.itemWidth / 2) {
                    //设置无法左划
                    if (index == this.hotWallData.length - 1) {
                        return;
                    }
                    hotWall.style.left =
                        this.hotWallLeftNum - this.itemWidth + "px";
                    //设置背景图
                    this.showSongUrl = this.hotWallData[index + 1].songUrl;
                    //设置热评墙页码储存在vuex
                    this.setHotWallPageNum(index + 1);
                    //设置目前页码
                    this.nowShowNum = this.hotWallPageNum + 1;
                }
                //右划
                else if (chaNum >= this.itemWidth / 2) {
                    // 第一个不允许向右划
                    if (this.hotWallLeftNum == "0") {
                        return;
                    }
                    hotWall.style.left =
                        this.hotWallLeftNum + this.itemWidth + "px";
                    //设置背景图
                    this.showSongUrl = this.hotWallData[index - 1].songUrl;
                    //设置热评墙页码储存在vuex
                    this.setHotWallPageNum(index - 1);
                    //设置目前页码
                    this.nowShowNum = this.hotWallPageNum + 1;
                }
            }
        }
    }
};
</script>

<style lang="less" scoped>
</style>